.fader {
  position: relative;
  width: 100%;
  padding-top: 60%;
  font-family: "futura", arial;
  overflow: hidden;
}
.fader .slide {
  position: absolute;
  width: 100%;
  top: 0;
  z-index: 1;
  opacity: 0;
  height: 100%;
}
.fader .slide img {
  width: 100%;
  height: 100%;
}
.fader .prev, .fader .next {
  position: absolute;
  height: 80px;
  line-height: 55px;
  width: 50px;
  font-size: 100px;
  text-align: center;
  color: #fff;
  top: 50%;
  left: 50px;
  z-index: 4;
  margin-top: -25px;
  cursor: pointer;
  opacity: 0;
  -webkit-transition: all 150ms;
  transition: all 150ms;
}
.fader .next {
  left: auto;
  right: 50px;
}
.fader .pager_list {
  position: absolute;
  width: 100%;
  height: 40px;
  padding: 0;
  line-height: 40px;
  bottom: 0;
  text-align: center;
  z-index: 4;
}
.fader .pager_list li {
  display: inline-block;
  width: 15px;
  height: 15px;
  margin: 0 7px;
  background: #fff;
  opacity: .3;
  text-indent: -9999px;
  border-radius: 999px;
  cursor: pointer;
  -webkit-transition: all 150ms;
  transition: all 150ms;
}
.fader .pager_list li:hover, .fader .pager_list li.active {
  opacity: 1;
}
.banner:hover .fader_controls .page.prev {
  opacity: 0.5;
  left: 0
}
.banner:hover .fader_controls .page.next {
  opacity: 0.5;
  right: 0
}
